"use client";

import { useState } from 'react';
import { useCart } from '@/context/CartContext';

export default function CarritoPage() {
  const { items, removeItem, clearCart, getTotalPrice } = useCart();
  const [nombre, setNombre] = useState('');
  const [direccion, setDireccion] = useState('');
  const [message, setMessage] = useState('');
  const [errors, setErrors] = useState({ nombre: '', direccion: '' });

  const validateForm = () => {
    const newErrors = { nombre: '', direccion: '' };
    let isValid = true;

    if (!nombre.trim()) {
      newErrors.nombre = 'El nombre es obligatorio';
      isValid = false;
    }

    if (!direccion.trim()) {
      newErrors.direccion = 'La direcci칩n de env칤o es obligatoria';
      isValid = false;
    }

    setErrors(newErrors);
    return isValid;
  };

  const handleComprar = () => {
    if (!validateForm() || items.length === 0) {
      return;
    }

    // Crear el contenido del email
    const itemsList = items.map(item => 
      `- ${item.name} (${item.category}) - Cantidad: ${item.quantity} - Precio: S/ ${item.price.toFixed(2)} - Subtotal: S/ ${(item.price * item.quantity).toFixed(2)}`
    ).join('%0A');

    const emailSubject = `Nueva Orden - ${nombre.trim()} - S/ ${getTotalPrice().toFixed(2)}`;
    
    const emailBody = `Nueva orden de compra - Tienda Frescura%0A%0A` +
      `INFORMACI칍N DEL CLIENTE:%0A` +
      `Nombre: ${nombre.trim()}%0A` +
      `Direcci칩n de env칤o: ${direccion.trim()}%0A%0A` +
      `PRODUCTOS PEDIDOS:%0A` +
      `${itemsList}%0A%0A` +
      `TOTAL DEL PEDIDO: S/ ${getTotalPrice().toFixed(2)}%0A%0A` +
      `---%0A` +
      `Este pedido fue generado autom치ticamente desde la tienda online.%0A` +
      `Fecha: ${new Date().toLocaleString('es-PE')}`;

    // Crear la URL de Gmail
    const gmailUrl = `https://mail.google.com/mail/?view=cm&fs=1&to=esanromanvargas@gmail.com&su=${encodeURIComponent(emailSubject)}&body=${emailBody}`;

    // Abrir Gmail en una nueva pesta침a
    window.open(gmailUrl, '_blank');

    // Mostrar mensaje de confirmaci칩n y limpiar carrito
    setMessage('춰Se ha abierto Gmail para enviar tu pedido! Por favor completa el env칤o del correo.');
    clearCart();
    setNombre('');
    setDireccion('');
  };

  const isFormValid = nombre.trim() && direccion.trim() && items.length > 0;

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-gray-900 mb-4">
            Carrito de Compras
          </h1>
        </div>

        {items.length === 0 ? (
          <div className="text-center py-12">
            <div className="text-6xl mb-4">游</div>
            <h2 className="text-2xl font-semibold text-gray-900 mb-2">
              Tu carrito est치 vac칤o
            </h2>
            <p className="text-gray-600 mb-6">
              Agrega algunos productos para comenzar tu compra
            </p>
            <a
              href="/tienda"
              className="inline-block bg-green-600 hover:bg-green-700 text-white font-medium py-3 px-6 rounded-lg transition-colors"
            >
              Ir a la Tienda
            </a>
          </div>
        ) : (
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            {/* Lista de productos */}
            <div className="lg:col-span-2">
              <div className="bg-white rounded-lg shadow-md p-6">
                <h2 className="text-2xl font-semibold text-gray-900 mb-6">
                  Productos en tu carrito
                </h2>
                
                <div className="space-y-4">
                  {items.map((item) => (
                    <div
                      key={item.id}
                      className="flex items-center justify-between p-4 border border-gray-200 rounded-lg"
                    >
                      <div className="flex-1">
                        <h3 className="font-semibold text-gray-900">{item.name}</h3>
                        <p className="text-sm text-gray-600">{item.category}</p>
                        <p className="text-sm text-gray-600">
                          Cantidad: {item.quantity} | Precio unitario: S/ {item.price.toFixed(2)}
                        </p>
                      </div>
                      <div className="flex items-center space-x-4">
                        <span className="font-semibold text-green-600">
                          S/ {(item.price * item.quantity).toFixed(2)}
                        </span>
                        <button
                          onClick={() => removeItem(item.id)}
                          className="text-red-600 hover:text-red-800 font-medium"
                        >
                          Eliminar
                        </button>
                      </div>
                    </div>
                  ))}
                </div>

                <div className="mt-6 pt-6 border-t border-gray-200">
                  <div className="flex justify-between items-center text-xl font-bold">
                    <span>Total:</span>
                    <span className="text-green-600">S/ {getTotalPrice().toFixed(2)}</span>
                  </div>
                </div>

                <button
                  onClick={clearCart}
                  className="mt-4 w-full bg-red-600 hover:bg-red-700 text-white font-medium py-2 px-4 rounded-lg transition-colors"
                >
                  Vaciar Carrito
                </button>
              </div>
            </div>

            {/* Formulario de checkout */}
            <div className="lg:col-span-1">
              <div className="bg-white rounded-lg shadow-md p-6">
                <h2 className="text-2xl font-semibold text-gray-900 mb-6">
                  Informaci칩n de Env칤o
                </h2>

                <div className="space-y-4">
                  <div>
                    <label htmlFor="nombre" className="block text-sm font-medium text-gray-700 mb-1">
                      Nombre completo *
                    </label>
                    <input
                      type="text"
                      id="nombre"
                      value={nombre}
                      onChange={(e) => setNombre(e.target.value)}
                      className={`w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-green-500 ${
                        errors.nombre ? 'border-red-500' : 'border-gray-300'
                      }`}
                      placeholder="Ingresa tu nombre completo"
                    />
                    {errors.nombre && (
                      <p className="mt-1 text-sm text-red-600">{errors.nombre}</p>
                    )}
                  </div>

                  <div>
                    <label htmlFor="direccion" className="block text-sm font-medium text-gray-700 mb-1">
                      Direcci칩n de env칤o *
                    </label>
                    <textarea
                      id="direccion"
                      value={direccion}
                      onChange={(e) => setDireccion(e.target.value)}
                      rows={3}
                      className={`w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-green-500 ${
                        errors.direccion ? 'border-red-500' : 'border-gray-300'
                      }`}
                      placeholder="Ingresa tu direcci칩n completa"
                    />
                    {errors.direccion && (
                      <p className="mt-1 text-sm text-red-600">{errors.direccion}</p>
                    )}
                  </div>

                  <button
                    onClick={handleComprar}
                    disabled={!isFormValid}
                    className={`w-full py-3 px-4 rounded-lg font-medium transition-colors ${
                      isFormValid
                        ? 'bg-green-600 hover:bg-green-700 text-white'
                        : 'bg-gray-300 text-gray-500 cursor-not-allowed'
                    }`}
                  >
                    Realizar Pedido
                  </button>

                  {message && (
                    <div className={`p-3 rounded-md text-sm ${
                      message.includes('exitosamente') 
                        ? 'bg-green-100 text-green-800' 
                        : 'bg-red-100 text-red-800'
                    }`}>
                      {message}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
